import React, { Component } from 'react';
import Axios from 'axios';

class AxiosAPI extends Component {
  constructor(){
    super();
  }

  componentDidMount() {
    console.log('Entró a Mount');
    //Implementación de Axios para consumir API por método GET - HTTP
    Axios.get('http://localhost:9000/clientes')
    .then(function (response) {
      // handle success
      console.log(response);
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    })
    .finally(function () {
      // always executed
    });
    // por POST
    Axios.post('http://localhost:9000/personas', {
      nombre: 'Orlando',
      edad: 'El Titulo',
    })
    .then(function (response) {
      console.log(response);
    })
    .catch(function (error) {
      console.log(error);
    });
  }
  render() {
    return(<div></div>)
  }
}

export default AxiosAPI;