import React, { Component } from 'react';
import Axios from 'axios';
import { Table } from 'reactstrap';

class Midata extends Component {
  constructor(props) {
    super(props);
  }

  state = {
    miData: []
  }

  componentDidMount() {
    console.log('Entró a mi Data');

    // Make a request for a user with a given ID
    Axios.get('http://localhost:9000/clientes')
    .then((response) => {
      // handle success
      this.setState({miData: response.data})
      console.log(response);
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    });
  }

  render() {
    return(
      <div>
        <h2>Información de datos desde API</h2>
        <Table dark hover>
          <thead>
            <tr>
              <th>#</th>
              <th>Nombres</th>
              <th>Documento</th>
              <th>Profesión</th>
            </tr>
          </thead>
          <tbody>
            {
              this.state.miData.map((dato,i) => (
                <tr>
                  <th scope="row">{ dato.id }</th>
                  <td>{ dato.nombres }</td>
                  <td>{ dato.documento }</td>
                  <td>{ dato.profesion }</td>
              </tr>
              ))
            }
          </tbody>
        </Table>
      </div>
    )
  }
}

export default Midata;