import React, {Component} from 'react';
import MisPostsVista from './misPostsVista';

class MisPosts extends Component{
  constructor(props) {
    super(props);
  }

  //Creamos objeto donde se almacen los valores consumidos de la API
  state = {
    posts: []
  }
  //Función encargada de consultar los posts
  componentDidMount() {
  fetch('https://jsonplaceholder.typicode.com/posts')
    .then(res => res.json())
    .then((data) => {
      this.setState({ posts: data});
    })
    .catch(console.log)
  }
  render() {
    return (
      <MisPostsVista posts={this.state.posts} /> 
    )
  }
}

export default MisPosts;
